.skills-section {
  padding: 40px 20px;
  text-align: center;
  background-color: #f9f9f9;
}

.skills-section h2 {
  font-size: 2.5rem;
  margin-bottom: 30px;
  color: #333;
}

.skills-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(140px, 1fr));
  gap: 20px;
  justify-items: center;
}

.skill-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-decoration: none; /* Removes underline */
  background-color: #fff;
  padding: 15px;
  border-radius: 12px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.08);
  transition: transform 0.2s ease, box-shadow 0.3s ease;
  color: #333; /* Makes text inherit dark color */
}

.skill-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.15);
}

.skill-card img {
  width: 60px;
  height: 60px;
  object-fit: contain;
  margin-bottom: 10px;
}

.skill-card span {
  font-size: 1rem;
  font-weight: 500;
}
